@import url("https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css");
@import url('https://fonts.googleapis.com/css2?family=Ruwudu&display=swap');

* {
  box-sizing: border-box;
}

/* GLOBAL STYLES */

body,
.App {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  font-size: 16px;
  background: url(https://www.toptal.com/designers/subtlepatterns/patterns/lightpaperfibers.png);
}

button {
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  cursor: pointer;
  color: #2563eb;
  transition: color 0.2s ease-in;
}

button:hover {
  color: #04c;
}

.App {
  display: flex;
}

/* SIDEBAR STYLES */

.app-sidebar {
  width: 30%;
  height: 100vh;
  border-right: 1px solid #ddd;
  background-color: #f1f5f9;
}

.app-sidebar-header {
  display: flex;
  justify-content: space-between;
  padding: 25px;
}

.app-sidebar-header .logo {
  margin: 0;
  font-family: 'Ruwudu', serif;
  font-size: 30px;
}

.app-sidebar-notes {
  height: calc(100vh - 78px);
  overflow-y: scroll;
}

.app-sidebar-notes::-webkit-scrollbar {
  width: 5px;
  background-color: transparent;
}

.app-sidebar-notes::-webkit-scrollbar-thumb {
  background: #f2f2f2;
  border-radius: 7px;
}

.app-sidebar-note {
  padding: 25px;
  cursor: pointer;
}

.sidebar-note-title {
  display: flex;
  justify-content: space-between;
}

.app-sidebar-note button {
  color: crimson;
}

.app-sidebar-note p {
  margin: 10px 0;
}

.app-sidebar-note small {
  display: block;
  color: #999;
}

.app-sidebar-note:hover {
  background: #ddd;
}

.app-sidebar-note.active,
.app-sidebar-note.active small {
  background: #08c;
  color: white;
  border-radius: 7px;
}

/* NOTE EDITOR/PREVIEW (MAIN) STYLES */

.app-main {
  width: 70%;
  height: 100vh;
  background-color: #f7fafc;
}

.app-main-note-edit,
.app-main-note-preview {
  height: 50vh;
}

.no-active-note {
  width: 70%;
  height: 100vh;
  line-height: 100vh;
  text-align: center;
  font-size: 2rem;
  color: #999;
}

/* Editing */
.app-main-note-edit {
  padding: 25px;
}

.app-main-note-edit input,
textarea {
  display: block;
  border: 1px solid #ddd;
  margin-bottom: 20px;
  width: 100%;
  height: calc(50vh - 130px);
  padding: 10px;
  resize: none;
  font-size: inherit;
  font-family: inherit;
  border-radius: 7px;
  background-color: #f8fafc;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
    5px 5px 16px 0px rgba(0, 0, 0, .1),
    4px 4px 5px 0px rgba(0, 0, 0, .1);
}

.app-main-note-edit input {
  height: 50px;
  font-size: 1.8rem;
}

/* Preview */
.app-main-note-preview {
  border-top: 1px solid #ddd;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.02);
  background-color: #f8fafc;
}

.preview-title {
  padding: 25px 25px 0 25px;
  margin: 0;
}

.markdown-preview {
  padding: 0 25px 25px 25px;
  font-size: 1rem;
  line-height: 2rem;
}

.icon {
  font-size: 30px;
}

.extra {
  opacity: 0;
  transition: opacity 0.25s ease;
  margin-top: 4px;
}

button:hover .extra {
  /* background-color: blue; */
  opacity: 1;
}

#delete-btn {
  transition: all 0.2s ease-in;
}

#delete-btn:hover {
  color: #2f2f2f;
}

.confirmDelete {
  position: absolute;
  top: 0;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.deleteBox {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #2f2f2f;
  height: 30vh;
  width: 40vw;
  color: white;
  border-radius: 12px;
  box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
    7px 7px 20px 0px rgba(0, 0, 0, .1),
    4px 4px 5px 0px rgba(0, 0, 0, .1);
}

.deleteBox button {
  height: 40px;
  background-color: #f7fafc;
  margin: 10px 5px;
  width: 80px;
  border-radius: 12px;
}

@media screen and (max-width: 500px) {
  .app-sidebar-header {
    flex-wrap: wrap;
    padding: 20px;
  }

  .app-sidebar-note {
    padding: 12px;
  }

  .deleteBox {
    height: 25vh;
    width: 60vw;
  }

  .icon {
    font-size: 20px;
  }

  .app-sidebar-note p {
    font-size: 14px;
  }
}

@media (max-width: 700px) and (min-width: 500px) {
  .app-sidebar-note {
    padding: 15px;
  }

  .icon {
    font-size: 22px;
  }
}